#main-about {
    width: 90%;
    font-family: "Poppins",sans-serif;
    margin: 3rem auto 0 auto;


}

#main-about h2 {
    font-size: 35px;
    margin-bottom: 2rem;
    text-align: center;
    text-transform: uppercase;
}

#main-about .text {
    text-align: justify;
}

#main-about p {
    font-size: 1.3rem;
    line-height: 1.4;
    margin-bottom: 1.6rem;

}