@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

.bgi 
{
    background-image: url("../../../Assets/main/bg_banner.jpg");
    background-size: cover;
    font-family: "Poppins", sans-serif;
    font-size: 1.4rem; 
    margin: 0%;
    color: var(--white);
    padding: 5% 10%;

}

.cgreen 
{
    font-size: 2.8rem;
    color: var(--white);
}

.banner_child
{
    text-align: justify;
}

@media screen and (max-width: 520px)
{
    .bgi 
    {
        font-size: 1rem; 
        padding: 5% 7%;
    }

    .banner_child
    {
        line-height: 1.6;
    }

    .cgreen 
    {
        font-size: 1.8rem; 
    }
}
